export default function Header() {
  return (
    <>
    <header className='pt-10 pb-10 xl:mx-30  flex items-center justify-between '>
    <Link to="/" ><img src={Cname} alt="" className=" w-40 xl:w-60 h-auto ml-3 " /></Link>
    <div className='flex gap-10'>
    <ul className=' hidden xl:flex items-center gap-8 text-lg '>
      <li><Link to="/services"><div className="flex items-center ">Our Service <IoIosArrowDown /></div></Link></li>
      
      <li><Link to="/industries"><div className="flex items-center ">Industries<IoIosArrowDown /></div></Link></li>
      <li><Link to="/OurWork"><div className="">Our Work</div></Link></li>
      <li><Link to="/Join"><div className="">Join US</div></Link></li>
      <li><Link to="/About"><div className="">About</div></Link></li>
    </ul>
    <div className="mr-10  text-3xl" onClick={()=>setisMeauOpen(!isMeauOpen)}>{isMeauOpen ? <FiX/> : <FiMenu/>}</div>
    </div>

    <div
  className={`absolute z-[100]  left-0 w-[100%] h-full  bg-red-100 gap-8 transition-all duration-500 ease-in-out ${isMeauOpen ? 'top-0' : '-top-full'}`}>
   <div className=" flex mr-10 xl:mr-40 flex-row-reverse mt-12  text-3xl   " onClick={()=>setisMeauOpen(!isMeauOpen)}><FiX/></div>
    
    <div className="flex">
      <ul className="gap-4 text-xl m-2 pl-10 flex flex-col ">
        <li><Link to="/services"><div className="flex items-center p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Our Service <IoIosArrowDown /></div></Link></li>
        <li><Link to="/industries"><div className="flex items-center p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Industries <IoIosArrowDown /></div></Link></li>
        <li><Link to="/OurWork"><div className="p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Our Work</div></Link></li>
        <li><Link to="/Join"><div className="p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Join Us</div></Link></li>
        <li><Link to="/About"><div className="p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>About</div></Link></li>
      </ul>
      <ul className="gap-0 text-xl m-2 pl-10 flex flex-col ">
        <li><Link to="/services"><div className="flex items-center p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Process </div></Link></li>
        <li><Link to="/industries"><div className="flex items-center p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Careers </div></Link></li>
        <li><Link to="/OurWork"><div className="p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Blogs</div></Link></li>
        <li><Link to="/Join"><div className="p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Whitepapers</div></Link></li>
        <li><Link to="/About"><div className="p-2" onClick={() => setisMeauOpen(!isMeauOpen)}>Contact Us</div></Link></li>
      </ul>
    </div>
    <ul className="gap-4 text-xl  pl-10 flex flex-col bg-indigo-300">
        <li><Link to="/services"><div className="flex items-center p-2" >social media</div></Link></li>
        
    </ul>
    </div>
    </header>
    </>
  );
}
